<template>
  <div class="app-container" id="carDevices">
    <div class="filter-container">
      <el-input v-model="listQuery.licenseNo" placeholder="车牌号/设备号/4G卡号/大队名称/imsi" class="filter-item" @keyup.enter.native="handleFilter" />
      <el-button class="filter-item" type="primary" icon="el-icon-search" @click="handleFilter">搜索</el-button>
      <el-button class="filter-item" type="primary" @click="handleCreate" v-if="power==1" icon="el-icon-circle-plus-outline">添加</el-button>
      <el-button class="filter-item" type="primary" @click="handleCreateAll" v-if="power==1" icon="iconfont iconpiliangtianjia">批量添加</el-button>

    </div>
    <div class="tableBox">
      <el-table :key="tableKey" v-loading="listLoading" :data="list" fit highlight-current-row style="width: 100%;" :row-class-name="tableRowClassName">
        <el-table-column label="序号" prop="id" fixed sortable="custom" align="center" width="80" type="index" :index="indexMethod"></el-table-column>

        <el-table-column label="设备号" width="200px" align="center">
          <template slot-scope="{row}">
            <span v-if="power==1" class="link-type" @click="handleUpdate(row)">{{ row.serialNo}}</span>
            <span v-else>{{ row.serialNo }}</span>
          </template>
        </el-table-column>
        <el-table-column label="车牌号" width="200px" align="center">
          <template slot-scope="{row}">
            <span>{{ row.licenseNo }}</span>
          </template>
        </el-table-column>
        <el-table-column label="4G卡号" width="200px" align="center" v-if="power==1">
          <template slot-scope="{row}">
            <span>{{ row.chinaMobileNo }}</span>
          </template>
        </el-table-column>
        <!-- <el-table-column label="所属大队" width="200px" align="center">
          <template slot-scope="{row}">
            <span>{{ row.teamName }}</span>
          </template>
        </el-table-column> -->

        <el-table-column label="imsi" width="150px" align="center" v-if="power==1">
          <template slot-scope="{row}">
            <span>{{ row.imsi }}</span>
          </template>
        </el-table-column>

        <el-table-column label="通道名称" min-width="100px" align="center">
          <template slot-scope="{row}">
            <span>{{ row.channelName }}</span>
          </template>
        </el-table-column>
        <el-table-column label="通道数" min-width="80px" align="center">
          <template slot-scope="{row}">
            <span>{{ row.channelNumber!==-1?row.channelNumber:""}}</span>
          </template>
        </el-table-column>
        <el-table-column label="状态" min-width="80px" align="center">
          <template slot-scope="{row}">
            <span>{{ row.status==2?'报废':"正常"}}</span>
          </template>
        </el-table-column>

        <el-table-column label="操作" align="center" min-width="400" fixed="right" class-name="small-padding fixed-width" v-if="power==1">
          <template slot-scope="{row}">
            <el-button type="primary" size="mini" @click="handleBind(row)" icon="iconfont iconsuo" :disabled="row.teamId!=-1&&row.carId!=-1">绑定</el-button>
            <el-button type="warning" size="mini" @click="handleUnbind(row)" :disabled="row.teamId==-1&&row.carId==-1" icon="iconfont iconjiesuo5">解绑</el-button>
            <el-button type="primary" size="mini" @click="handleUpdate(row)" :disabled="row.status==2" icon="el-icon-edit-outline">编辑</el-button>

            <el-button type="danger" size="mini" @click="deleteConfirmOpen(row)" :disabled="row.status==2" icon="iconfont iconbaofei">报废</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <pagination v-show="total>0" :total="total" :page.sync="listQuery.page" :limit.sync="listQuery.limit" @pagination="getList" layout="prev, pager, next, sizes, total, jumper" />

    <el-dialog id="myDialog" @close="scrollTop" :title="textMap[dialogStatus]" :visible.sync="dialogFormVisible" center :class="{'editClass':(dialogStatus=='update')}">
      <el-form ref="dataForm" :rules="rules" :model="temp" label-position="left" class="formStyle">

        <el-form-item label="设备号" prop="serialNo" v-if="dialogStatus=='create'">
          <el-input v-model="temp.serialNo" />
        </el-form-item>
        <el-form-item label="4G卡号" prop="chinaMobileNo">
          <el-input v-model="temp.chinaMobileNo" />
        </el-form-item>
        <el-form-item label="imsi" prop="imsi">
          <el-input v-model="temp.imsi" />
        </el-form-item>
        <el-form-item label="通道名称" prop="channelName">
          <el-input v-model="temp.channelName" />
        </el-form-item>
        <el-form-item label="通道数" prop="channelNumber">
          <el-select v-model="temp.channelNumber" class="filter-item" placeholder="Please select">
            <el-option v-for="item in channelNoOptions" :key="item.channelNo" :label="item.channelName" :value="item.channelNo" />
          </el-select>
        </el-form-item>

      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="dialogStatus==='create'?createData():updateData()">确定</el-button>
      </div>
    </el-dialog>
    <el-dialog id="myDialog" @close="scrollTop" title="批量添加" :visible.sync="dialogVisible" center>
      <el-form ref="addAllForm" :rules="rules1" :model="addAlltemp" label-position="left" class="addAllForm formStyle">
        <el-form-item label="设备号头" prop="head">
          <el-input v-model="addAlltemp.head" placeholder="(不包括后四位,例如“CX051234”请输入“CX05”)" class="carNo" />
        </el-form-item>

        <el-form-item label="后四位开始" prop="begin">
          <el-input v-model.number="addAlltemp.begin" />
        </el-form-item>
        <el-form-item label="后四位结束" prop="end">
          <el-input v-model.number="addAlltemp.end" />
        </el-form-item>
        <el-form-item label="通道数" prop="channelNumber">
          <el-select v-model="addAlltemp.channelNumber" class="filter-item" placeholder="Please select">
            <el-option v-for="item in channelNoOptions" :key="item.channelNo" :label="item.channelName" :value="item.channelNo" />
          </el-select>
        </el-form-item>
        <!-- <div style="color:#FF3F3F;text-align:center;margin-top:8px;">提示：在本系统添加成功后需要到车载定位系统批量添加</div> -->
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="createAllCar()">确定</el-button>
      </div>
    </el-dialog>
    <el-dialog id="myDialog" @close="scrollTop" :title="bindMap[bindDialogStatus]" :visible.sync="BindDialogFormVisible" center>
      <el-form ref="bindForm" :rules="bindRules" label-position="left" class="bindForm formStyle" :model="bindTemp">
        <el-form-item :label="`${bindMap[bindDialogStatus]}类型`" prop="type">
          <el-select filterable v-model="bindTemp.type" class="filter-item" placeholder="Please select">
            <el-option v-for="item in bindTypeOptions" :key="item.id" :label="item.name" :value="item.id" :disabled="item.disabled" />
          </el-select>
        </el-form-item>
        <el-form-item label="绑定车辆" prop="carId" v-if="bindTemp.type==1&&bindDialogStatus=='bind'">
          <el-select filterable v-model="bindTemp.carId " class="filter-item" placeholder="Please select">
            <el-option v-for="item in carOptions" :key="item.id" :label="item.licenseNo" :value="item.id" />
          </el-select>
        </el-form-item>
        <el-form-item label="所属大队" prop="teamId" v-if="bindTemp.type==2&&bindDialogStatus=='bind'">
          <el-select filterable v-model="bindTemp.teamId " class="filter-item" placeholder="Please select">
            <el-option v-for="item in largeTeamOptions" :key="item.id" :label="item.teamName" :value="item.id" />
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="bindDialogStatus=='bind'?bindSubmit():unbindSubmit()">确定</el-button>

      </div>
    </el-dialog>
  </div>
</template>

<script>
import Pagination from "@/components/Pagination"; // secondary package based on el-pagination
//车辆列表，添加车辆，编辑，删除，搜索，批量添加车辆
import {
  carlist,
  carAdd,
  cardelete,
  carupdate,
  carSearch,
  addBatch,
  unbindCar,
  bindCar,
  discardDev,
  bindTeam,
  unbindTeam,
  permitBind
} from "../../../api/managementArchives/carDevices";
import {
  teamAndCarList

} from "../../../api/realTimeMonitoring";
import { formatDate } from "../../../../src/common"; //格式化时间
//大队下拉框数据，车组下拉框数据
import {
  teamsSelect,
  exportExcelReq
} from "../../../api/managementArchives/common";

const carUsageOptions = [
  {
    key: 1,
    display_name: "使用中"
  },
  {
    key: 2,
    display_name: "维修中"
  },
  {
    key: 3,
    display_name: "保养中"
  },
  {
    key: 4,
    display_name: "报废"
  }
];

//自定义设备号头验证规则
const reasonableLen = (rule, value, callback) => {
  const reasonableLength = /[0-9]{4}/;
  if (!reasonableLength.test(value)) {
    callback(new Error("长度为4"));
  } else {
    callback();
  }
};
//未选择
const unSelect = (rule, value, callback) => {
  if (value == -1) {
    callback(new Error("请选择"));
  } else {
    callback();
  }
};
let myLoading = undefined; // 弹窗服务调用
export default {
  name: "carDevices",
  components: {
    Pagination
  },
  data() {
    let multiRule = (rule, value, callback) => {
      // 多设备验证
      let cars = this.imeiCar
      if (cars.length < 1) {
        callback(new Error("请选择设备"));
      } else {
        callback();
      }
    };
    return {
      power: JSON.parse(sessionStorage.getItem("userInfo")).Power, // 权限
      tableKey: 0,
      list: null, //车辆列表
      total: 0, //车辆总数
      listLoading: true,
      indexMethod: 1, // 序号
      listQuery: {
        page: 1,
        limit: 10,
        licenseNo: undefined
      },
      carUsageOptions, //车辆使用情况下拉数据
      runOptions: [
        {
          key: 0,
          display_name: "入库"
        },
        {
          key: 1,
          display_name: "上路"
        }
      ], //运行情况下拉数据
      groupsOptions: [], //车组下拉数据
      carOptions: [],
      largeTeamOptions: [], //大队下拉数据
      channelNoOptions: [], //通道个数下拉数据
      //添加或编辑数据
      temp: {

        serialNo: undefined
      },
      dialogFormVisible: false, //添加或编辑窗口显隐
      dialogStatus: "", //控制是编辑窗口还是添加窗口
      textMap: {
        update: "编辑",
        create: "添加"
      },
      //添加或编辑表单校验
      rules: {

        serialNo: [
          {
            required: true,
            message: "请填写设备编号",
            trigger: "blur"
          }
        ]
      },
      //批量添加表单校验
      rules1: {
        head: [
          {
            required: true,
            message: "请填写设备号头",
            trigger: "blur"
          }
        ],
        begin: [
          {
            required: true,
            message: "请填写设备号后四位区间头",
            trigger: "blur"
          },
          { validator: reasonableLen, trigger: "blur" }
        ],
        end: [
          {
            required: true,
            message: "请填写设备号后四位区间尾",
            trigger: "blur"
          },
          { validator: reasonableLen, trigger: "blur" }
        ],
        channelNo: [
          {
            message: "请选择通道数",
            trigger: "change",
            required: true
          }
        ]
      },
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now();
        },
        shortcuts: [
          {
            text: "今天",
            onClick(picker) {
              picker.$emit("pick", new Date());
            }
          },
          {
            text: "昨天",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24);
              picker.$emit("pick", date);
            }
          },
          {
            text: "一周前",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", date);
            }
          }
        ]
      },
      dialogVisible: false, //批量添加窗口显隐
      addAlltemp: {
        head: null,
        begin: null,
        end: null,
        channelNo: null
      },
      dataCar: [], // 车辆列表
      imeiCar: [],
      imei2name: {}, // 设备的imei和对应的名字
      bindTypeOptions: [
        { id: -1, name: "请选择" },
        { id: 1, name: "车辆" },
        { id: 2, name: "大队" }

      ], //绑定类型下拉数据
      bindTemp: {

        type: 1
      },

      BindDialogFormVisible: false,
      bindDialogStatus: "",//控制是绑定还是解绑窗口
      bindMap: {
        bind: "绑定",
        unbind: "解绑"
      },
      bindRules: {
        type: [
          { required: true, message: "请选择绑定类型", trigger: "change", validator: unSelect }
        ],
        imeiCar: [{ validator: multiRule, trigger: "change", required: true, message: "请选择车辆", }],
        teamId: [
          { required: true, message: "请选择绑定大队", trigger: "change", validator: unSelect }
        ]
      },

    };
  },
  created() {
    this.getTeamAndCarList();
    this.getList(); //初始化获取车辆列表
    this.getSelect(); //初始化弹窗的下拉框


    this.getChannelNoOptions(); //初始化通道下拉数据
  },
  mounted() { },
  methods: {
    deleteConfirmOpen(row) {
      this.$confirm('确定进行此操作吗?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.discard(row)
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消'
        });
      });
    },
    handleBind(row) {
      permitBind({ deviceId: row.id }).then(res => {
        if (res.status == 0) {
          if (res.data == null) {
            this.carOptions = [];
            this.carOptions.unshift({ id: -1, licenseNo: "请选择" });
            return;
          }
          this.carOptions = res.data;
          this.carOptions.unshift({ id: -1, licenseNo: "请选择" });
        } else {
          if (res.data == null) {
            this.carOptions = [];
            this.carOptions.pop();
          }
          this.$message.error(res.msg);
        }
      });
      this.bindTypeOptions = [
        { id: -1, name: "请选择" },
        { id: 1, name: "车辆" },
        { id: 2, name: "大队" }

      ];
      this.bindDialogStatus = "bind";
      this.bindTemp = {
        type: 1,
        deviceId: row.id,
        carId: row.carId,
        teamId: row.teamId
      }
      this.BindDialogFormVisible = true;
    },
    handleUnbind(row) {
      if (row.carId == -1) { this.bindTypeOptions[1].disabled = true }
      if (row.teamId == -1) { this.bindTypeOptions[2].disabled = true }
      this.bindDialogStatus = "unbind";
      this.bindTemp = {
        deviceId: row.id,
        type: 1
      }
      this.BindDialogFormVisible = true;
    },

    bindSubmit() {
      this.$refs["bindForm"].validate(valid => {
        if (valid) {
          myLoading = this.$loading({ target: "#myDialog .el-dialog" });
          let subData, request;
          if (this.bindTemp.type == 1) {
            subData = {
              deviceId: this.bindTemp.deviceId,
              carId: this.bindTemp.carId,
            }
            request = bindCar;

          } else if (this.bindTemp.type == 2) {
            subData = {
              deviceId: this.bindTemp.deviceId,
              teamId: this.bindTemp.teamId,
            }
            request = bindTeam;
          }
          request(subData)
            .then(res => {
              if (res.status == 0) {
                this.getList();
                this.getSelect(); //初始化弹窗的下拉框
                this.$message.success(res.msg);
                this.BindDialogFormVisible = false;
              } else {
                this.$message.error(res.msg);
              }
            })
            .finally(() => {
              myLoading.close();
            });

        }
      })
    },
    unbindSubmit() {
      this.$refs["bindForm"].validate(valid => {
        if (valid) {
          myLoading = this.$loading({ target: "#myDialog .el-dialog" });

          let subData, request;
          subData = {
            deviceId: this.bindTemp.deviceId
          }
          if (this.bindTemp.type == 1) {

            request = unbindCar;

          } else if (this.bindTemp.type == 2) {

            request = unbindTeam;


          }
          request(subData)
            .then(res => {
              if (res.status == 0) {
                this.getList();
                this.$message.success(res.msg);
                this.BindDialogFormVisible = false;
              } else {
                this.$message.error(res.msg);
              }
            })
            .finally(() => {
              myLoading.close();
            });
        }
      })
    },
    getTeamAndCarList() {
      let that = this;
      teamAndCarList({}).then(res => {
        if (res.status == 0) {
          that.dataCar = this.formatCas(res.data, "id", "licenseNo"); // 设置车下拉树的数据格式
          console.warn(that.dataCar)
        } else {
          this.$message.error(res.msg);
        }
      });
    },


    formatCas(data, imei, name) {
      // 格式化级联选择器数据 data是原始数据 imei是vaue值 name是label值
      let result = [],
        id = 0,
        _t = this;
      for (let i in data) {
        if (i == "报废车辆") { continue };
        let childs = []; // 格式化子数组
        data[i].forEach(x => {
          childs.push({
            value: x[imei],
            label: x[name]
          });
          _t.imei2name[x[imei]] = x[name]; // 将设备号与设备名组成一个数组
        });
        result.push({
          value: `i${id++}`,
          label: i == "" ? "未分组" : i,
          children: childs
        });
      }
      return result;
    },
    //车辆列表
    getList() {
      let _t = this;
      _t.listLoading = true;
      carlist({
        page: this.listQuery.page,
        limit: this.listQuery.limit,
        status: [0, 1]
      })
        .then(res => {
          if (res.status == 0) {
            console.log(res.data);
            this.listLoading = false;
            this.list = res.data;

            this.total = res.count;
            this.indexMethod = (_t.listQuery.page - 1) * _t.listQuery.limit + 1;
          } else {
            this.$message.error(res.msg);
          }
        })
        .finally(() => {
          _t.listLoading = false;
        });
    },

    //初始化通道数据
    getChannelNoOptions() {
      this.channelNoOptions = [];
      this.channelNoOptions.push({ channelName: '请选择', channelNo: -1 })
      for (let i = 0; i < 17; i++) this.channelNoOptions.push({ channelName: i, channelNo: i });
    },


    //搜素
    handleFilter() {
      if (
        this.listQuery.licenseNo == "" ||
        this.listQuery.licenseNo == undefined
      ) {
        this.listQuery.licenseNo = null;
        this.getList();
        return;
      }
      carSearch({
        keyword: this.listQuery.licenseNo
      }).then(res => {
        if (res.status == 0) {
          this.listQuery.page = 1;
          this.listLoading = false;
          this.list = [];
          this.list = res.data;
          this.total = 0;
        } else {
          this.$message.error(res.msg);
        }
      });
    },

    //重置添加或编辑数据
    resetTemp() {
      this.temp = {

        serialNo: undefined
      }
    },
    //重置批量添加数据
    resetAddAllTemp() {
      this.addAlltemp = {
        head: null,
        begin: null,
        end: null,
        channelNumber: -1
      };
    },
    //点击添加
    handleCreate() {
      this.resetTemp();
      this.dialogStatus = "create";
      this.dialogFormVisible = true;
      this.$nextTick(() => {
        this.$refs["dataForm"].clearValidate();
      });
    },

    //点击批量添加
    handleCreateAll() {
      this.resetAddAllTemp();
      this.dialogVisible = true;
      this.$nextTick(() => {
        this.$refs["addAllForm"].clearValidate();
      });
    },
    //添加
    createData() {
      this.$refs["dataForm"].validate(valid => {
        if (valid) {
          myLoading = this.$loading({ target: "#myDialog .el-dialog" });


          carAdd(this.temp)
            .then(res => {
              if (res.status == 0) {
                this.getList();
                this.$message.success("添加成功");
                this.dialogFormVisible = false;
                // //实时监控检测到变化重新获取车辆和枪支车牌号或枪支号和imei号放入carAndGunObj，及时更新信息
                // this.$store.commit("realTimeMonitor/ISEDITFILES", true);
              } else {
                this.$message.error(res.msg);
              }
            })
            .finally(() => {
              myLoading.close();
            });
        } else {
          return false;
        }
      });
    },
    //批量添加
    createAllCar() {
      this.$refs["addAllForm"].validate(valid => {
        if (valid) {
          myLoading = this.$loading({ target: "#myDialog .el-dialog" });
          addBatch(this.addAlltemp)
            .then(res => {
              if (res.status == 0) {
                // this.listQuery.page = 1;
                this.getList();
                this.$message.success("添加成功");
                this.dialogVisible = false;
                //实时监控检测到变化重新获取车辆和枪支车牌号或枪支号和imei号放入carAndGunObj，及时更新信息
                // this.$store.commit("realTimeMonitor/ISEDITFILES", true);
              } else {
                this.$message.error(res.msg);
              }
            })
            .finally(() => {
              myLoading.close();
            });
        } else {
          return false;
        }
      });
    },

    //点击编辑
    handleUpdate(row) {
      this.temp = Object.assign({}, row); // copy obj
      this.dialogStatus = "update";
      this.dialogFormVisible = true;
      this.$nextTick(() => {
        this.$refs["dataForm"].clearValidate();
      });
    },

    //编辑
    updateData() {
      this.$refs["dataForm"].validate(valid => {
        if (valid) {
          myLoading = this.$loading({ target: "#myDialog .el-dialog" });
          let subObj = {
            id: this.temp.id,
            chinaMobileNo: this.temp.chinaMobileNo,
            imsi: this.temp.imsi,
            channelName: this.temp.channelName,
            channelNumber: this.temp.channelNumber,
          }
          carupdate(subObj)
            .then(res => {
              if (res.status == 0) {
                this.getList();
                this.$message.success("编辑成功");
                this.dialogFormVisible = false;
                // //实时监控检测到变化重新获取车辆和枪支车牌号或枪支号和imei号放入carAndGunObj，及时更新信息
                // this.$store.commit("realTimeMonitor/ISEDITFILES", true);
              } else {
                this.$message.error(res.msg);
              }
            })
            .finally(() => {
              myLoading.close();
            });
        } else {
          return false;
        }
      });
    },

    //报废
    discard(row) {
      let _t = this;
      _t.listLoading = true;
      discardDev({
        id: row.id
      })
        .then(res => {
          if (res.status == 0) {
            if (_t.listQuery.page > 1 && _t.list.length <= 1) {
              // 最后一条返回上一页
              _t.listQuery.page -= 1;
            }
            this.getList();
            _t.$message.success("报废成功");
            // //实时监控检测到变化重新获取车辆和枪支车牌号或枪支号和imei号放入carAndGunObj，及时更新信息
            // this.$store.commit("realTimeMonitor/ISEDITFILES", true);
          } else {
            this.$message.error(res.msg);
            _t.listLoading = false;
          }
        })
        .catch(err => {
          _t.listLoading = false;
          console.error(err);
        });
    },


    //初始化下拉框
    getSelect() {
      var that = this;
      teamsSelect({ status: 1 }).then(res => {
        if (res.status == 0) {
          if (res.data == null) {
            this.largeTeamOptions = [];
            this.largeTeamOptions.unshift({ id: -1, teamName: "请选择" });
            return;
          }
          that.largeTeamOptions = res.data;
          that.largeTeamOptions.unshift({ id: -1, teamName: "请选择" });
        } else {
          if (res.data == null) {
            this.largeTeamOptions = [];
            this.largeTeamOptions.pop();
          }
          this.$message.error(res.msg);
        }
      });


    },

    scrollTop() {
      // 弹窗滚动到顶部
      let myDialog = document.querySelector("#myDialog .el-dialog__body");
      myDialog.scrollTop = 0;
    },
    tableRowClassName({ row, rowIndex }) {
      if (rowIndex % 2 == 0) {
        return "color-row";
      } else if (rowIndex % 2 == 1) {
        return "noColor-row";
      }

      return "";
    },

  }
};
</script>

