import request from '../../request'
//设备列表
export function carlist(data) {
    return request({
        url: '/carDevices/page',
        method: 'post',
        data
    })
}
//添加
export function carAdd(data) {
    return request({
        url: '/carDevices/addOne',
        method: 'post',
        data
    })
}
//删除
export function cardelete(data) {
    return request({
        url: '/carDevices/deleteOne',
        method: 'post',
        data
    })
}
//编辑
export function carupdate(data) {
    return request({
        url: '/carDevices/update',
        method: 'post',
        data,
    })
}
//搜索设备
export function carSearch(data) {
    return request({
        url: '/carDevices/fuzzyQuery',
        method: 'post',
        data
    })
}
//批量添加
export function addBatch(data) {
    return request({
        url: '/carDevices/addBatch',
        method: 'post',
        data
    })
}


//绑定设备
export function bindCar(data) {
    return request({
        url: '/carDevices/bind',
        method: 'post',
        data
    })
}
//解绑设备
export function unbindCar(data) {
    return request({
        url: '/carDevices/unbind',
        method: 'post',
        data,
    })
}
//报废设备
export function discardDev(data) {
    return request({
        url: '/carDevices/scrap',
        method: 'post',
        data,
    })
}
//绑定大队
export function bindTeam(data) {
    return request({
        url: '/carDevices/bindTeam',
        method: 'post',
        data
    })
}
//解绑大队
export function unbindTeam(data) {
    return request({
        url: 'carDevices/unbindTeam',
        method: 'post',
        data,
    })
}
//根据设备所属大队匹配相同大队的车辆，同时未绑定大队的车辆也会被返回
export function permitBind(data) {
    return request({
        url: '/cars/permitBind',
        method: 'post',
        data,
    })
}